<template>
  <div class="CapacityToPredict">
    <div class="predictionBox">
      <p class="titleText">
        <span class="lineBg"></span>
        <span></span>
      </p>
      <el-table :data="predictionData" border height="600">
        <el-table-column prop="datatime" label="" header-align="center" align="center"/>
        <el-table-column prop="enbid" label="Enbid" header-align="center" align="center"/>
        <el-table-column prop="cgi" label="Cellid" header-align="center" align="center"/>
        <el-table-column prop="avgErab" label="E-RAB(KB)" header-align="center" align="center"/>
        <el-table-column prop="numRpc" label="RRC" header-align="center" align="center"/>
        <el-table-column prop="ratePusch" label="PUSCH" header-align="center" align="center"/>
        <el-table-column prop="ratePdsch" label="PDSCH" header-align="center" align="center"/>
        <el-table-column prop="ratePdcch" label="PDCCH" header-align="center" align="center"/>
        <el-table-column prop="upData" label="(GB)" header-align="center" align="center"/>
        <el-table-column prop="downData" label="(GB)" header-align="center" align="center"/>
      </el-table>
    </div>
    <hr/>
    <!--<div class="trafficDataTableBox">
      <p class="titleText">
        <span class="lineBg"></span>
        <span></span>
      </p>
      <el-table :data="trafficDataTable" border>
        <el-table-column prop="enodebName" label="ID" header-align="center" align="center"/>
        <el-table-column prop="sector" label="ID" header-align="center" align="center"/>
        <el-table-column label="" header-align="center" align="center">
          <el-table-column prop="enbid1" label="Enbid" header-align="center" align="center"/>
          <el-table-column prop="cgi1" label="Cellid" header-align="center" align="center"/>
          <el-table-column prop="workFrequencyBand1" label="" header-align="center" align="center"/>
          <el-table-column prop="carrier1" label="" header-align="center" align="center"/>
        </el-table-column>
        <el-table-column label="" header-align="center" align="center">
          <el-table-column prop="enbid2" label="Enbid" header-align="center" align="center"/>
          <el-table-column prop="cgi2" label="Cellid" header-align="center" align="center"/>
          <el-table-column prop="workFrequencyBand2" label="" header-align="center" align="center"/>
          <el-table-column prop="carrier2" label="" header-align="center" align="center"/>
        </el-table-column>
      </el-table>
    </div>
    <hr/>-->
    <div class="expansionPlanListBox">
      <p class="titleText">
        <span class="lineBg"></span>
        <span></span>
      </p>
      <el-table :data="expansionPlanList" border>
        <el-table-column label="" header-align="center" align="center">
          <el-table-column prop="enodebName" label="ID" header-align="center" align="center"/>
          <el-table-column prop="cellName" label="" header-align="center" align="center"/>
          <el-table-column prop="sector" label="ID" header-align="center" align="center"/>
          <el-table-column prop="coverageType" label="" header-align="center" align="center"/>
          <el-table-column prop="workFrequencyBand" label="" header-align="center" align="center"/>
          <el-table-column prop="newWorkFrequencyBand" label="" header-align="center" align="center"/>
        </el-table-column>
        <el-table-column label="" header-align="center" align="center">
          <el-table-column prop="carrier" label="" header-align="center" align="center"/>
          <el-table-column prop="needCarrier" label="" header-align="center" align="center"/>
          <el-table-column prop="gap" label="GAP" header-align="center" align="center"/>
        </el-table-column>
        <!--<el-table-column label="">
          <el-table-column prop="expansionPlan" label="" header-align="center" align="center"/>
          <el-table-column prop="expansionWorkFrequencyBand" label="" header-align="center" align="center"/>
        </el-table-column>-->
        <!--<el-table-column label="">
          <el-table-column type="selection">

          </el-table-column>
        </el-table-column>-->
      </el-table>
    </div>
  </div>
</template>

<script>
    export default {
        name: "CapacityToPredict",
        data() {
            return {
                //
                predictionData: [],
                //
                trafficDataTable: [],
                //
                expansionPlanList: [],
            }
        },
        methods: {
            //1 predictionData
            getData() {
                let _t = this;
                _t.$api.post('/forecast/get', {}, function (res) {
                    if (res) {
                        _t.predictionData = res;
                    }
                })
            },
            //
            getDataTwo() {
                let _t = this;
                _t.$api.post('/forecast/getSmallDifferenceFlowForms', {}, function (res) {
                    if (res) {
                        _t.trafficDataTable = res;
                    }
                })
            },
            //
            getDataThree() {
                let _t = this;
                _t.$api.post('/forecast/getSectorExpansionPlanForm', {}, function (res) {
                    if (res) {
                        _t.expansionPlanList = res;
                    }
                })
            },
        },
        created() {
            let _t = this;
            //1
            _t.getData();
            //
            _t.getDataTwo();
            //
            _t.getDataThree();
        }
    }
</script>

<style scoped>

</style>



