                        formItem.midPdcch = _t.busyTableData[1].pdcCh.trim(); //  
                        formItem.midUpStream = _t.busyTableData[1].upStream.trim(); // 
                        formItem.midDownStream = _t.busyTableData[1].downStream.trim(); // 
                        formItem.minErab = _t.busyTableData[3].eRab.trim(); //  
                        formItem.minRpc = _t.busyTableData[3].RRC.trim(); //  rrc
                        formItem.minPusch = _t.busyTableData[3].pusCh.trim(); //  
                        formItem.minPdsch = _t.busyTableData[3].pdsCh.trim(); //  
                        formItem.minPdcch = _t.busyTableData[3].pdcCh.trim(); //  
                        formItem.minUpStream = _t.busyTableData[3].upStream.trim(); //  
                        formItem.minDownStream = _t.busyTableData[3].downStream.trim(); //  
                        _t.$api.post('/params/saveSuperBusyInfo', {
                            id: _t.busyTableForm.id,
                            maxErab: formItem.maxErab, // 
                            maxRpc: formItem.maxRpc,  //
                            maxPusch: formItem.maxPusch,
                            maxPdsch: formItem.maxPdsch,
                            maxPdcch: formItem.maxPdcch,
                            maxUpStream: formItem.maxUpStream,
                            maxDownStream: formItem.maxDownStream,
                            midErabLt: formItem.midErabLt, //
                            midErabGt: formItem.midErabGt, //
                            midRpc: formItem.midRpc,
                            midPusch: formItem.midPusch,
                            midPdsch: formItem.midPdsch,
                            midPdcch: formItem.midPdcch,
                            midUpStream: formItem.midUpStream,
                            midDownStream: formItem.midDownStream,
                            minErab: formItem.minErab,
                            minRpc: formItem.minRpc,
                            minPusch: formItem.minPusch,
                            minPdsch: formItem.minPdsch,
                            minPdcch: formItem.minPdcch,
                            minUpStream: formItem.minUpStream,
                            minDownStream: formItem.minDownStream
                        }, function (res) {
                            if (res === true) {
                                _t.$message.success('');
                            } else {
                                _t.$message.error('');
                            }
                        });
                    } else {
                        _t.$alert('E-RAB,!', '');
                    }
                }
            },
            //
            getDataFive() {
                let _t = this;
                _t.$api.post('/params/getSuperBusyInfo', {}, function (res) {
                    if (res.length !== 0) {
                        _t.busyTableForm.id = res[0].id;
                        // 
                        _t.busyTableData[0].eRab = res[0].maxErab.toString(); //  
                        _t.busyTableData[0].RRC = res[0].maxRpc.toString();  // rrc
                        _t.busyTableData[0].pusCh = res[0].maxPusch.toString(); // 
                        _t.busyTableData[0].pdsCh = res[0].maxPdsch.toString(); // 
                        _t.busyTableData[0].pdcCh = res[0].maxPdcch.toString(); // 
                        _t.busyTableData[0].upStream = res[0].maxUpStream.toString(); // 
                        _t.busyTableData[0].downStream = res[0].maxDownStream.toString(); // 
                        _t.busyTableData[1].eRab = res[0].midErabLt.toString(); // 
                        _t.busyTableData[2].eRab = res[0].midErabGt.toString(); // 
                        _t.busyTableData[1].RRC = res[0].midRpc.toString(); // rrc
                        _t.busyTableData[1].pusCh = res[0].midPusch.toString(); //  
                        _t.busyTableData[1].pdsCh = res[0].midPdsch.toString(); //  
                        _t.busyTableData[1].pdcCh = res[0].midPdcch.toString(); //  
                        _t.busyTableData[1].upStream = res[0].midUpStream.toString(); // 
                        _t.busyTableData[1].downStream = res[0].midDownStream.toString(); // 
                        _t.busyTableData[3].eRab = res[0].minErab.toString(); //  
                        _t.busyTableData[3].RRC = res[0].minRpc.toString(); //  rrc
                        _t.busyTableData[3].pusCh = res[0].minPusch.toString(); //  
                        _t.busyTableData[3].pdsCh = res[0].minPdsch.toString(); //  
                        _t.busyTableData[3].pdcCh = res[0].minPdcch.toString(); //  
                        _t.busyTableData[3].upStream = res[0].minUpStream.toString(); //  
                        _t.busyTableData[3].downStream = res[0].minDownStream.toString(); //  
                    }
                })
            },
            // 
            arraySpanMethod({rowIndex, columnIndex}) {
                if (rowIndex === 1 && columnIndex !== 1) {
                    return {
                        rowspan: 2,
                        colspan: 1
                    }
                }
            },
            // 
            handleClick(row, index) {
                var _t = this;
                var isSetNum = 0;
                _t.busyTableData.forEach((item) => {
                    if (item.isSet === true) {
                        isSetNum++;
                    }
                });
                // 
                if (isSetNum === 0 || row.isSet === true) {
                    // 
                    if (row.isSet === true) {
                        // 
                        if (index === 1) {
                            if (row.eRab.trim() === ''
                                || _t.busyTableData[2].eRab.trim() === ''
                                || row.pusCh.trim() === ''
                                || row.pdsCh.trim() === ''
                                || row.pdcCh.trim() === ''
                                || row.upStream.trim() === ''
                                || row.downStream.trim() === ''
                                || row.RRC.trim() === '') {
                                _t.$alert('!', '');
                            } else {
                                row.isSet = !row.isSet;
                                _t.busyTableData[2].isSet = !_t.busyTableData[2].isSet;
                            }
                        } else {
                            // 
                            if (row.eRab.trim() === ''
                                || row.pusCh.trim() === ''
                                || row.pdsCh.trim() === ''
                                || row.pdcCh.trim() === ''
                                || row.upStream.trim() === ''
                                || row.downStream.trim() === ''
                                || row.RRC.trim() === '') {
                                _t.$alert('!', '');
                            } else {
                                row.isSet = !row.isSet;
                            }
                        }
                    } else {
                        // 
                        row.isSet = !row.isSet;
                        // 
                        if (index === 1) {
                            _t.busyTableData[2].isSet = !_t.busyTableData[2].isSet;
                        }
                    }
                } else {
                    _t.$alert(',!', '');
                }
            },
        },
        created() {
            let _t = this;
            //
            _t.getDataOne();
            //
            _t.getDataTwo();
            //-
            _t.getDataThree();
            //
            _t.getDataFour();
            //
            _t.getDataFive();
        }
    }
</script>

<style scoped>
  /*.btnCla{
    clear: both;
    float: right;
  }*/
</style>
<style>
  .titleTime .el-radio {
    line-height: 40px;
  }
</style>



