<template>
  <div class="areaSetting_Box">
    <!---->
    <div>
      <p class="titleText">
        <span class="lineBg"></span>
        <span></span>
        <span class="des">:LTE,D,D, D > FA > FDD1800 > FDD900</span>
      </p>
      <div v-loading="isLoading" element-loading-text="" id="container"></div>
    </div>
    <hr>
    <!---->
    <div>
      <p class="titleText">
        <span class="lineBg"></span>
        <span></span>
      </p>
      <el-table :data="sectorData" border height="400">
        <el-table-column prop="enodebName" label="ID" header-align="center" align="center"/>
        <el-table-column prop="sector" label="ID" header-align="center" align="center"/>
        <el-table-column prop="enbid" label="Enbid" header-align="center" align="center"/>
        <el-table-column prop="cellId" label="Cellid" header-align="center" align="center"/>
        <el-table-column prop="siteType" label="" header-align="center" align="center"/>
        <el-table-column prop="workFrequencyBand" label="" header-align="center" align="center"/>
        <el-table-column prop="vendor" label="" header-align="center" align="center"/>
        <el-table-column prop="supportCarriers" label="(TD-LTE 20M)" header-align="center" align="center"/>
        <el-table-column prop="usedCarriers" label="(TDD 20M)" header-align="center" align="center"/>
        <el-table-column prop="softExpandedCarriers" label="" header-align="center" align="center"/>
      </el-table>
    </div>

    <!---->
    <el-drawer
      title=""
      append-to-body
      :visible.sync="drawerVisible"
      direction="rtl"
      :before-close="handleClose">
      <div class="padRight20">
        <el-form :model="formItem" label-width="100px">
          <el-form-item label="ECI">
            <el-input v-model="formItem.eci" disabled/>
          </el-form-item>
          <el-form-item label="CGI">
            <el-input v-model="formItem.cgi" disabled/>
          </el-form-item>
          <el-form-item label="E-NODEB">
            <el-input v-model="formItem.enodebName" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.cellName" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.tac" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.localCellId" disabled/>
          </el-form-item>
          <el-form-item label="ENB_ID">
            <el-input v-model="formItem.enbid" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.longitude" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.latitude" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.coverageType" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.azimuth" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.antennaHeight" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.totalDownTiltAngle" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.carrierFfrequencyNum" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.workFrequencyBand" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.pci" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.maxTransmitPower" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.coverScene" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.provinceName" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.cityName" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.districtandcounty" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.vendor" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.electronicTiltAngle" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.mechanicalTiltAngle" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.isCoreArea" disabled/>
          </el-form-item>
          <el-form-item label="3D-MIMO">
            <el-input v-model="formItem.support3DMimo" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.siteType" disabled/>
          </el-form-item>
          <el-form-item label="(TDD 20M)">
            <el-input v-model="formItem.supportCarriers" disabled/>
          </el-form-item>
          <el-form-item label="(TDD 20M)">
            <el-input v-model="formItem.usedCarriers" disabled/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formItem.softExpandedCarriers" disabled/>
          </el-form-item>
        </el-form>
      </div>
    </el-drawer>
  </div>
</template>

<script>

    export default {
        name: "areaSettings",
        data() {
            return {
                isLoading: false,
                // 
                sectorData: [],
                // 
                calculateTableData: [],
                // -
                FrequencyTableData: [],
                // 
                drawerVisible: false,
                // 
                formItem: {
                    eci: '', //
                    cgi: '', // CGI
                    enodebName: '', // E-NODEB
                    cellName: '', // 
                    tac: '', // 
                    localCellId: '', // 
                    enbid: '', // enbId
                    longitude: '', // 
                    latitude: '', // 
                    coverageType: '', // 
                    azimuth: '', // 
                    antennaHeight: '', // 
                    totalDownTiltAngle: '', // 
                    carrierFfrequencyNum: '', // 
                    workFrequencyBand: '', // 
                    pci: '', // 
                    maxTransmitPower: '', // 
                    coverScene: '', // 
                    provinceName: '', // 
                    cityName: '', // 
                    districtandcounty: '', // 
                    vendor: '', // 
                    electronicTiltAngle: '', // 
                    mechanicalTiltAngle: '', // 
                    isCoreArea: '', // 
                    support3DMimo: '', // 3D-MIMO
                    siteType: '', // 
                    supportCarriers: '', // TDD 20M
                    usedCarriers: '', // TDD 20M
                    softExpandedCarriers: '', // 
                }
            }
        },
        methods: {
            // 
            getMap() {
                var _t = this;
                //  
                _t.isLoading = true;
                _t.$api.post('/region/get1', {}, function (res) {
                    if (res !== undefined && res !== null) {
                        var pathIn = []; // 
                        var pathOut = []; // 
                        var pathPoints = []; // 
                        // AMap.Map.Marker
                        var map = new AMap.Map('container', {//'container'id
                            resizeEnable: true, //
                            zoom: 8, //
                        });
                        // 
                        map.clearMap();

                        //  min:
                        if (res.min !== undefined && res.min !== null && res.min.length !== 0) {
                            res.min.forEach((item) => {
                                var minArr = new Array();
                                minArr[0] = item.x_longitude;
                                minArr[1] = item.y_latitude;
                                pathIn.push(minArr);
                            });
                        }
                        // max:
                        if (res.max !== undefined && res.max !== null && res.max.length !== 0) {
                            res.max.forEach((item) => {
                                var maxArr = new Array();
                                maxArr[0] = item.x_longitude;
                                maxArr[1] = item.y_latitude;
                                pathOut.push(maxArr);
                            });
                        }
                        // points: 
                        if (res.points !== undefined && res.points !== null && res.points.length !== 0) {
                            res.points.forEach((item) => {
                                var obj = new Object();
                                var pointsArr = new Array();
                                pointsArr[0] = item.x_longitude;
                                pointsArr[1] = item.y_latitude;
                                obj.points = pointsArr;
                                obj.eci = item.eci ? item.eci : null;
                                pathPoints.push(obj);
                            });
                        }

                        var polygonIn = new AMap.Polygon({
                            path: pathIn, // 
                            strokeColor: "#FF33FF", // 
                            strokeWeight: 0,
                            strokeOpacity: 0.2,
                            fillOpacity: 0.4,
                            fillColor: "#1791fc",
                            zIndex: 50,
                        });

                        var polygonOut = new AMap.Polygon({
                            path: pathOut,
                            strokeColor: "#f22",
                            strokeWeight: 0,
                            strokeOpacity: 0.2,
                            fillOpacity: 0.4,
                            fillColor: '#f22',
                            zIndex: 49,
                        });
                        map.add(polygonIn);
                        map.add(polygonOut);
                        // 
                        map.setFitView([polygonIn]);
                        pathPoints.forEach((item) => {
                            // 
                            var marker = new AMap.Marker({
                                icon: new AMap.Icon({
                                    image: require('../../assets/img/map/points.png'),
                                    size: new AMap.Size(6, 6),  //
                                    imageSize: new AMap.Size(6, 6)
                                }),
                                position: item.points, //  
                                offset: new AMap.Pixel(-3, -3), // 
                            });
                            marker.setMap(map); // 
                            marker.off('click'); // 
                            // 
                            marker.on('click', function () {
                                _t.getAreaData(item);
                            });
                        });
                        //  
                        map.on("complete", function () {
                            _t.isLoading = false;
                        });
                    }
                });
            },
            // 
            getTableData() {
                let _t = this;
                _t.$api.post('/region/getSectorToCellCorrespondenceForm', {}, function (res) {
                    if (res) {
                        _t.sectorData = res;
                    }
                })
            },
            //  
            getAreaData(item) {
                var _t = this;
                _t.drawerVisible = true;
                _t.$api.post('/region/getLteInfoByEci', {
                    eci: item.eci
                }, function (res) {
                    if (res !== null) {
                        _t.formItem.eci = res.eci; // eci
                        _t.formItem.cgi = res.cgi; // CGI
                        _t.formItem.enodebName = res.enodebName; // E-NODEB
                        _t.formItem.cellName = res.cellName; // 
                        _t.formItem.tac = res.tac; // 
                        _t.formItem.localCellId = res.localCellId; // 
                        _t.formItem.enbid = res.enbid; // enbId
                        _t.formItem.longitude = res.longitude; // 
                        _t.formItem.latitude = res.latitude; // 
                        _t.formItem.coverageType = res.coverageType; // 
                        _t.formItem.azimuth = res.azimuth; // 
                        _t.formItem.antennaHeight = res.antennaHeight; // 
                        _t.formItem.totalDownTiltAngle = res.totalDownTiltAngle; // 
                        _t.formItem.carrierFfrequencyNum = res.carrierFfrequencyNum; // 
                        _t.formItem.workFrequencyBand = res.workFrequencyBand; // 
                        _t.formItem.pci = res.pci; // 
                        _t.formItem.maxTransmitPower = res.maxTransmitPower; // 
                        _t.formItem.coverScene = res.coverScene; // 
                        _t.formItem.provinceName = res.provinceName; // 
                        _t.formItem.cityName = res.cityName; // 
                        _t.formItem.districtandcounty = res.districtandcounty; // 
                        _t.formItem.vendor = res.vendor; // 
                        _t.formItem.electronicTiltAngle = res.electronicTiltAngle; // 
                        _t.formItem.mechanicalTiltAngle = res.mechanicalTiltAngle; // 
                        _t.formItem.isCoreArea = res.isCoreArea; // 
                        _t.formItem.support3DMimo = res.support3DMimo; // 3D-MIMO
                        _t.formItem.siteType = res.siteType; // 
                        _t.formItem.supportCarriers = res.supportCarriers; // TDD 20M
                        _t.formItem.usedCarriers = res.usedCarriers; // TDD 20M
                        _t.formItem.softExpandedCarriers = res.softExpandedCarriers; // 
                    }
                });
            },
            // 
            handleClose() {
                var _t = this;
                _t.drawerVisible = false;
                _t.formItem.eci = ''; // eci
                _t.formItem.cgi = ''; // CGI
                _t.formItem.enodebName = ''; // E-NODEB
                _t.formItem.cellName = ''; // 
                _t.formItem.tac = ''; // 
                _t.formItem.localCellId = ''; // 
                _t.formItem.enbid = ''; // enbId
                _t.formItem.longitude = ''; // 
                _t.formItem.latitude = ''; // 
                _t.formItem.coverageType = ''; // 
                _t.formItem.azimuth = ''; // 
                _t.formItem.antennaHeight = ''; // 
                _t.formItem.totalDownTiltAngle = ''; // 
                _t.formItem.carrierFfrequencyNum = ''; // 
                _t.formItem.workFrequencyBand = ''; // 
                _t.formItem.pci = ''; // 
                _t.formItem.maxTransmitPower = ''; // 
                _t.formItem.coverScene = ''; // 
                _t.formItem.provinceName = ''; // 
                _t.formItem.cityName = ''; // 
                _t.formItem.districtandcounty = ''; // 
                _t.formItem.vendor = ''; // 
                _t.formItem.electronicTiltAngle = ''; // 
                _t.formItem.mechanicalTiltAngle = ''; // 
                _t.formItem.isCoreArea = ''; // 
                _t.formItem.support3DMimo = ''; // 3D-MIMO
                _t.formItem.siteType = ''; // 
                _t.formItem.supportCarriers = ''; // TDD 20M
                _t.formItem.usedCarriers = ''; // TDD 20M
                _t.formItem.softExpandedCarriers = ''; // 
            }
        },
        created() {
            let _t = this;
            _t.getMap();
            _t.getTableData();
        },
    }
</script>

<style scoped>
  .titleText span.des {
    font-size: 14px;
    font-weight: normal;
    margin-left: 50px;
  }

  #container {
    position: relative;
    width: 100%;
    height: 600px;
    padding: 20px;
  }
</style>



