                /*regionForm: {
                    radio: true,
                },*/
                regionTableData: [],
                // 
                /*statisForm: {
                    radio: true,
                },*/
                statisTableData: [],
                // D
                /*frequencyForm: {
                    radio: true,
                },*/
                frequencyTableData: [],
                // 
                /*terminalForm: {
                    radio: true,
                },*/
                //5g
                fiveData: [],
                dataCate: {
                    truncate: true
                }
            }
        },
        methods: {
            // 
            errorOne(err, file, fileList) {
                var _t = this;
                _t.$message.error('!');
            },
            //
            handleAvatarSuccess(val, file, fileList) {
                let _t = this;
                _t.$message.success('!');
                _t.getData();
            },
            // NR
            errorTwo(err, file, fileList) {
                var _t = this;
                _t.$message.error('!');
            },
            // 
            errorThree(err, file, fileList) {
                var _t = this;
                _t.$message.error('!');
            },
            // 
