                var _t = this;
                _t.$message.error('!');
            },
            // 
            errorFive(err, file, fileList) {
                var _t = this;
                _t.$message.error('!');
            },
            get_pane() {
                let _t = this;
                _t.workForm.radio = true;
            },
            getUpDate() {
                let _t = this;
                _t.dataCate.truncate = _t.workForm.radio;
            },
            //table
            getData() {
                let _t = this;
                _t.$api.post('/data/getLteInfo', {}, function (res) {
                    if (res) {
                        _t.wokeData = res;
                    }
                })
            },
            //
            handleAvatarSuccessHt(val, file, fileList) {
                let _t = this;
                _t.$message.success('!');
                _t.getDataHt();
            },
            //Table
            getDataHt() {
                let _t = this;
                _t.$api.post('/data/getTalkInfo', {}, function (res) {
                    if (res) {
                        _t.systemData = res;
                    }
                })
            },
            //
            handleAvatarSuccessLqg(val, file, fileList) {
                let _t = this;
                _t.$message.success('!');
                _t.getDataLqg();
            },
            //Table
            getDataLqg() {
                let _t = this;
                _t.$api.post('/data/getNeighborRelationshipInfo', {}, function (res) {
                    if (res) {
                        _t.regionTableData = res;
                    }
                })
            },
            //
            handleAvatarSuccessLqt(val, file, fileList) {
                let _t = this;
                _t.$message.success('!');
                _t.getDataLqg();
            },
            //Table
            getDataLqt() {
                let _t = this;
                _t.$api.post('/data/getNeighborStatisticsInfo', {}, function (res) {
                    if (res) {
                        _t.statisTableData = res;
                    }
                })
            },
            // 
            handleAvatarSuccessZd(val, file, fileList) {
                let _t = this;
            },
            // 5g
            handleAvatarSuccess5G(val, file, fileList) {
                let _t = this;
                _t.$message.success('!');
                _t.getData5g();
            },
            getData5g() {
                let _t = this;
                _t.$api.post('/data/getNrInfo', {}, function (res) {
                    if (res) {
                        _t.fiveData = res;
                    }
                })
            }
        },
        created() {
            let _t = this;
            //
            // _t.getData();
            //
            // _t.getDataHt();
            //
            // _t.getDataLqg();
            //
            // _t.getDataLqt();
            /*//
            _t.getDataLZd();*/
            //5g
            // _t.getData5g();
        },

    }
</script>

